import React, { useState } from 'react';
import { IntroContainer, IntroParagraph, IntroSmallHeader, ComponentWrapper, ControlBox } from './global-intro.styled';
import Dropdown from 'components/molecules/Dropdown/Dropdown';

interface menu {
  name: string;
  link: string;
}

const DropdownIntro: React.FC = () => {
  const defaultMenus: Array<menu> = [
    {
      name: 'button',
      link: '#',
    },
    {
      name: 'input',
      link: '#',
    },
    {
      name: 'header',
      link: '#',
    },
  ];
  const [input, setInput] = useState('');
  const [menus, setMenus] = useState(defaultMenus);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const newMenu: menu = {
      name: input,
      link: '#',
    };
    setMenus([...menus, newMenu]);
    setInput('');
  };

  const handleInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = event.target;
    setInput(value);
  };

  return (
    <IntroContainer>
      <IntroParagraph>asdf</IntroParagraph>

      <IntroSmallHeader>Overview</IntroSmallHeader>
      <ComponentWrapper>
        <Dropdown menu={menus} leftPos={50} />
      </ComponentWrapper>
      <ControlBox>
        <form onSubmit={handleSubmit}>
          <input type="text" value={input} placeholder="값을 입력하세요" onChange={handleInput} />
          <input type="submit" value="추가" />
        </form>
      </ControlBox>
    </IntroContainer>
  );
};

export default DropdownIntro;
